import React, { useState, useEffect } from "react";
import "./experience.css";
import AOS from "aos";
import "aos/dist/aos.css";

const Experience = () => {
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [modalImage, setModalImage] = useState(null);

  useEffect(() => {
    AOS.init({ duration: 1000 });
  }, []);

  const handleImageClick = (image) => {
    setModalImage(image);
    setIsModalOpen(true);
  };

  const closeModal = () => {
    setIsModalOpen(false);
    setModalImage(null);
  };

  return (
    <div className="container">
      {/* Hero Section */}
      <section className="hero"></section>

      {/* Experience Header */}
      <section className="experience-header">
        <h1 className="experience-title">Professional Experience</h1>
        <p className="experience-intro">
          A journey of innovation, creativity, and leadership in software
          development and architecture.
        </p>
      </section>

      {/* Experience Timeline */}
      <section className="experience-timeline">
        <div className="timeline-item" data-aos="fade-right" onClick={() => handleImageClick("path/to/your/image1.jpg")}>
          <h2 className="timeline-role">Software Architect</h2>
          <h3 className="timeline-company">Tech Innovations Ltd. (2021 - Present)</h3>
          <img
            className="timeline-image"
            src="path/to/your/image1-thumbnail.jpg"
            alt="Software Architect"
          />
          <p className="timeline-description">
            Spearheading the architecture and development of scalable software solutions, with a focus on cutting-edge technology and cloud infrastructure.
          </p>
        </div>

        {/* Add other timeline items here in similar format */}

      </section>

      {/* Modal for Image */}
      {isModalOpen && (
        <div className="modal" onClick={closeModal}>
          <div className="modal-content">
            <img src={modalImage} alt="Experience" />
          </div>
        </div>
      )}

      {/* Footer */}
      <footer className="footer">
        <p>Â© 2024 Sanraj. All rights reserved.</p>
        <div className="social-icons">
          <a href="https://www.google.com/" className="social-icon" aria-label="Facebook">FB</a>
          <a href="https://www.google.com/" className="social-icon" aria-label="Twitter">TW</a>
          <a href="https://www.google.com/" className="social-icon" aria-label="Instagram">IG</a>
        </div>
      </footer>
    </div>
  );
};

export default Experience;
